<template>
  <div class="grammar-practice-page">
    <div class="container">
      <!-- È†ÇÈÉ®Â∞éËà™Âàó -->
      <div class="top-bar">
        <router-link to="/" class="back-btn">‚Üê ËøîÂõû</router-link>
        <h2 class="page-title">ÊñáÊ≥ïÁ∑¥Áøí</h2>
        <div class="today-count">ÊÆµËêΩÔºö{{ grammarStore.filteredCount }} / {{ grammarStore.totalPassages }}</div>
      </div>

      <!-- ÁØ©ÈÅ∏Âàó -->
      <div class="filter-bar">
        <div class="filter-group">
          <label>ÊñáÊ≥ï‰∏ªÈ°åÔºö</label>
          <select v-model="filterTopic" @change="applyFilter">
            <option value="">ÂÖ®ÈÉ®</option>
            <option v-for="topic in grammarStore.allTopics" :key="topic" :value="topic">
              {{ topic }}
            </option>
          </select>
        </div>

        <div class="filter-group">
          <label>ÂàÜÈ°ûÔºö</label>
          <select v-model="filterCategory" @change="applyFilter">
            <option value="">ÂÖ®ÈÉ®</option>
            <option v-for="cat in grammarStore.allCategories" :key="cat" :value="cat">
              {{ cat }}
            </option>
          </select>
        </div>

        <button
          @click="toggleReviewOnly"
          class="filter-btn"
          :class="{ active: showReviewOnly }"
        >
          üîÅ Âè™È°ØÁ§∫ÈúÄË§áÁøí ({{ grammarStore.reviewCount }})
        </button>
      </div>

      <!-- ËºâÂÖ•‰∏≠ -->
      <div v-if="grammarStore.isLoading" class="loading">
        ËºâÂÖ•‰∏≠...
      </div>

      <!-- ÈåØË™§Ë®äÊÅØ -->
      <div v-else-if="grammarStore.error" class="error">
        {{ grammarStore.error }}
      </div>

      <!-- ‰∏ªË¶ÅÁ∑¥ÁøíÂçÄ -->
      <div v-else-if="grammarStore.currentPassage" class="practice-area">
        <!-- ÊÆµËêΩË≥áË®ä -->
        <div class="passage-header">
          <div class="passage-info">
            <span class="passage-id">{{ grammarStore.currentPassage.passageId }}</span>
            <span class="grammar-topic">{{ grammarStore.currentPassage.grammarTopic }}</span>
            <span v-if="grammarStore.currentPassage.category" class="category">
              {{ grammarStore.currentPassage.category }}
            </span>
          </div>
          <button
            @click="toggleReview"
            class="review-btn"
            :class="{ active: grammarStore.currentPassage.needReview === 'Y' }"
          >
            {{ grammarStore.currentPassage.needReview === 'Y' ? 'üîÅ ÈúÄË§áÁøí' : '‚úì ‰∏çË§áÁøí' }}
          </button>
        </div>

        <!-- ÊÆµËêΩÈ°ØÁ§∫ÂçÄ -->
        <div class="passage-display" @click="showInfo = true">
          <p v-if="!isHidden" class="passage-text">
            {{ grammarStore.currentPassage.fullPassage }}
          </p>
          <p v-else class="passage-hidden">
            ÔºàÊÆµËêΩÂ∑≤Èö±ËóèÔºåÈªûÊìäÊí≠ÊîæÊåâÈàïËÅΩÂØ´Ôºâ
          </p>
          <p class="click-hint">ÈªûÊìäÊü•ÁúãÊñáÊ≥ïÂâñÊûêÂíåÂñÆÂ≠óËß£Èáã</p>
        </div>

        <!-- ÊéßÂà∂ÊåâÈàïÂàó -->
        <div class="control-bar">
          <button @click="toggleHidden" class="control-btn">
            {{ isHidden ? 'üëÅ È°ØÁ§∫' : 'üôà Èö±Ëóè' }}
          </button>

          <!-- Èü≥È†ªÊí≠ÊîæÂÖÉ‰ª∂ -->
          <SentenceAudioPlayer
            :sentence="grammarStore.currentPassage.fullPassage"
          />
        </div>

        <!-- ÊâìÂ≠óÁ∑¥ÁøíÂçÄ -->
        <TypingSentence
          :sentence="grammarStore.currentPassage.fullPassage"
          :isHidden="isHidden"
          @completed="onTypingCompleted"
        />

        <!-- Â∞éËà™ÊåâÈàï -->
        <div class="nav-bar">
          <button @click="prevPassage" class="nav-btn">
            ‚Üê ‰∏ä‰∏ÄÊÆµ
          </button>
          <span class="nav-progress">
            {{ currentPosition }} / {{ grammarStore.filteredCount }}
          </span>
          <button @click="nextPassage" class="nav-btn primary">
            ‰∏ã‰∏ÄÊÆµ ‚Üí
          </button>
        </div>

        <!-- ÈÄ≤Â∫¶Ë≥áË®ä -->
        <div class="progress-info">
          <span>Ë§áÁøíÊ∏ÖÂñÆÔºö{{ grammarStore.reviewCount }} ÊÆµ</span>
        </div>
      </div>

      <!-- Ê≤íÊúâÊÆµËêΩ -->
      <div v-else class="no-passage">
        <p>ÁõÆÂâçÊ≤íÊúâÊÆµËêΩÂèØÁ∑¥Áøí</p>
        <button @click="loadPassages" class="start-btn">ÈáçÊñ∞ËºâÂÖ•</button>
      </div>

      <!-- ÊñáÊ≥ïÂâñÊûêÂΩàÁ™ó -->
      <GrammarInfo
        v-if="showInfo && grammarStore.currentPassage"
        :passage="grammarStore.currentPassage"
        @close="showInfo = false"
      />
    </div>
  </div>
</template>

<script>
import { useGrammarStore } from '@/stores/grammarstore';
import { useExcelStore } from '@/stores/excelStore';
import TypingSentence from '@/components/TypingSentence.vue';
import SentenceAudioPlayer from '@/components/SentenceAudioPlayer.vue';
import GrammarInfo from '@/components/GrammarInfo.vue';
import * as XLSX from 'xlsx';

export default {
  name: 'GrammarPractice',
  components: {
    TypingSentence,
    SentenceAudioPlayer,
    GrammarInfo,
  },
  data() {
    return {
      grammarStore: useGrammarStore(),
      excelStore: useExcelStore(),
      isHidden: false,
      showInfo: false,
      filterTopic: '',
      filterCategory: '',
      showReviewOnly: false,
    };
  },
  computed: {
    currentPosition() {
      return this.grammarStore.currentIndex + 1;
    },
  },
  async created() {
    // ËºâÂÖ•ÂñÆÂ≠ó Excel
    await this.loadWordExcel();

    // ËºâÂÖ•ÊñáÊ≥ïÊÆµËêΩ
    await this.grammarStore.loadPassages();
  },
  methods: {
    // ËºâÂÖ•ÂñÆÂ≠ó ExcelÔºàÊ≤øÁî®ÁèæÊúâÈÇèËºØÔºâ
    async loadWordExcel() {
      if (Object.keys(this.excelStore.wordExplanations).length === 0) {
        try {
          const url = process.env.BASE_URL + 'excel/default.xlsx';
          const response = await fetch(url);
          const arrayBuffer = await response.arrayBuffer();
          const data = new Uint8Array(arrayBuffer);
          const workbook = XLSX.read(data, { type: 'array' });

          let allData = [];
          workbook.SheetNames.forEach(sheetName => {
            const worksheet = workbook.Sheets[sheetName];
            const jsonData = XLSX.utils.sheet_to_json(worksheet);
            allData = allData.concat(jsonData);
          });

          if (allData.length > 0) {
            this.excelStore.setExcelData(Object.keys(allData[0]), allData);
          }
        } catch (error) {
          console.error('ËºâÂÖ•ÂñÆÂ≠ó Excel Â§±Êïó:', error);
        }
      }
    },

    // ÂàáÊèõÈö±Ëóè/È°ØÁ§∫
    toggleHidden() {
      this.isHidden = !this.isHidden;
    },

    // ÂàáÊèõË§áÁøíÁãÄÊÖã
    async toggleReview() {
      if (this.grammarStore.currentPassage) {
        await this.grammarStore.toggleReview(this.grammarStore.currentPassage.passageId);
      }
    },

    // ‰∏ã‰∏ÄÊÆµ
    nextPassage() {
      this.grammarStore.nextPassage();
      this.showInfo = false;
    },

    // ‰∏ä‰∏ÄÊÆµ
    prevPassage() {
      this.grammarStore.prevPassage();
      this.showInfo = false;
    },

    // ÊâìÂ≠óÂÆåÊàê
    onTypingCompleted() {
      console.log('ÊÆµËêΩÊâìÂ≠óÂÆåÊàêÔºÅ');
    },

    // ÈáçÊñ∞ËºâÂÖ•
    loadPassages() {
      this.grammarStore.loadPassages();
    },

    // Â•óÁî®ÁØ©ÈÅ∏
    applyFilter() {
      this.grammarStore.setFilter({
        topic: this.filterTopic,
        category: this.filterCategory,
        reviewOnly: this.showReviewOnly,
      });
    },

    // ÂàáÊèõÂè™È°ØÁ§∫ÈúÄË§áÁøí
    toggleReviewOnly() {
      this.showReviewOnly = !this.showReviewOnly;
      this.applyFilter();
    },
  },
};
</script>

