import { defineStore } from 'pinia';
import * as XLSX from 'xlsx';

export const useSentenceStore = defineStore('sentence', {
  state: () => ({
    sentences: [],           // ÊâÄÊúâÁü≠Âè•
    currentLevel: '',        // ÁõÆÂâçÈõ£Â∫¶ (basic/intermediate/advanced)
    currentIndex: 0,         // ÁõÆÂâçÁ∑¥Âà∞Á¨¨ÂπæÂè•ÔºàÊñ∞Âè•Â≠êÔºâ
    todayCount: 0,           // ‰ªäÊó•Á∑¥ÁøíÊï∏Èáè
    newCount: 0,             // Êñ∞Âè•Â≠êË®àÊï∏ÔºàÈÅîÂà∞ 3 Â∞±ÊèíÂÖ•Ë§áÁøíÂè•Ôºâ
    currentSentence: null,   // ÁõÆÂâçÈ°ØÁ§∫ÁöÑÂè•Â≠ê
    isLoading: false,        // ËºâÂÖ•ÁãÄÊÖã
    error: null,             // ÈåØË™§Ë®äÊÅØ
  }),

  getters: {
    // ÈúÄË¶ÅË§áÁøíÁöÑÂè•Â≠êÂàóË°®
    reviewList: (state) => {
      return state.sentences.filter(s => s.needReview === 'Y');
    },

    // Á∏ΩÂè•Êï∏
    totalSentences: (state) => {
      return state.sentences.length;
    },

    // ÈúÄË¶ÅË§áÁøíÁöÑÂè•Êï∏
    reviewCount: (state) => {
      return state.sentences.filter(s => s.needReview === 'Y').length;
    },

    // Â∑≤Á∑¥ÁøíÁöÑÊñ∞Âè•Â≠êÊï∏
    learnedCount: (state) => {
      return state.currentIndex;
    },
  },

  actions: {
    // ËºâÂÖ•ÊåáÂÆöÈõ£Â∫¶ÁöÑÁü≠Âè• Excel
    async loadSentences(level) {
      this.isLoading = true;
      this.error = null;
      this.currentLevel = level;

      try {
        const fileName = `sentences-${level}.xlsx`;
        const url = process.env.BASE_URL + `excel/${fileName}`;
        const response = await fetch(url);

        if (!response.ok) {
          throw new Error(`Êâæ‰∏çÂà∞Ê™îÊ°àÔºö${fileName}`);
        }

        const arrayBuffer = await response.arrayBuffer();
        const data = new Uint8Array(arrayBuffer);
        const workbook = XLSX.read(data, { type: 'array' });

        // ËÆÄÂèñÁ¨¨‰∏ÄÂÄã sheet
        const sheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[sheetName];
        const jsonData = XLSX.utils.sheet_to_json(worksheet);

        // Ëß£ÊûêË≥áÊñô
        this.sentences = jsonData.map(row => ({
          id: row.id,
          sentence: row.sentence || '',
          targetWords: (row.targetWords || '').split(',').map(w => w.trim().toLowerCase()),
          grammar: row.grammar || '',
          category: row.category || '',
          needReview: row.needReview || 'N',
        }));

        // Âæû localStorage ËºâÂÖ•ÈÄ≤Â∫¶
        this.loadProgress(level);

        // Ê™¢Êü•ÊòØÂê¶ÈúÄË¶ÅÈáçÁΩÆ‰ªäÊó•Ë®àÊï∏
        this.checkDailyReset();

        console.log(`‚úÖ ËºâÂÖ• ${fileName} ÊàêÂäüÔºö${this.sentences.length} Âè•`);
        return true;

      } catch (error) {
        console.error('‚ùå ËºâÂÖ•Áü≠Âè• Excel Â§±Êïó:', error);
        this.error = error.message;
        return false;

      } finally {
        this.isLoading = false;
      }
    },

    // Âæû localStorage ËºâÂÖ•ÈÄ≤Â∫¶
    loadProgress(level) {
      const indexKey = `sentence-index-${level}`;
      const savedIndex = localStorage.getItem(indexKey);
      this.currentIndex = savedIndex ? parseInt(savedIndex, 10) : 0;

      const todayCountKey = 'sentence-today-count';
      const savedTodayCount = localStorage.getItem(todayCountKey);
      this.todayCount = savedTodayCount ? parseInt(savedTodayCount, 10) : 0;
    },

    // ÂÑ≤Â≠òÈÄ≤Â∫¶Âà∞ localStorage
    saveProgress() {
      const indexKey = `sentence-index-${this.currentLevel}`;
      localStorage.setItem(indexKey, this.currentIndex.toString());

      localStorage.setItem('sentence-today-count', this.todayCount.toString());
      localStorage.setItem('sentence-today-date', this.getTodayDate());
    },

    // ÂèñÂæó‰ªäÂ§©Êó•Êúü (YYYY-MM-DD)
    getTodayDate() {
      const now = new Date();
      return now.toISOString().split('T')[0];
    },

    // Ê™¢Êü•ÊòØÂê¶ÈúÄË¶ÅÈáçÁΩÆ‰ªäÊó•Ë®àÊï∏
    checkDailyReset() {
      const savedDate = localStorage.getItem('sentence-today-date');
      const today = this.getTodayDate();

      if (savedDate !== today) {
        this.todayCount = 0;
        this.newCount = 0;
        localStorage.setItem('sentence-today-date', today);
        localStorage.setItem('sentence-today-count', '0');
      }
    },

    // ÂèñÂæó‰∏ã‰∏ÄÂè•ÔºàÂê´ 3:1 Ë§áÁøíÈÇèËºØÔºâ
    getNextSentence() {
      if (this.sentences.length === 0) {
        this.currentSentence = null;
        return null;
      }

      this.newCount++;

      // ÊØè 3 Âè•Êñ∞ÁöÑÔºåÊèíÂÖ• 1 Âè•Ë§áÁøí
      if (this.newCount >= 3 && this.reviewList.length > 0) {
        // ÂæûË§áÁøíÊ∏ÖÂñÆÈö®Ê©üÊäΩ‰∏ÄÂè•
        const randomIndex = Math.floor(Math.random() * this.reviewList.length);
        this.currentSentence = this.reviewList[randomIndex];
        this.newCount = 0;
      } else {
        // ÂèñÊñ∞Âè•Â≠ê
        if (this.currentIndex >= this.sentences.length) {
          // Â∑≤Á∂ìÁ∑¥ÂÆåÂÖ®ÈÉ®ÔºåÂæûÈ†≠ÈñãÂßãÊàñÂè™Á∑¥Ë§áÁøí
          if (this.reviewList.length > 0) {
            const randomIndex = Math.floor(Math.random() * this.reviewList.length);
            this.currentSentence = this.reviewList[randomIndex];
          } else {
            // ÂÖ®ÈÉ®Á∑¥ÂÆå‰∏îÊ≤íÊúâË§áÁøíÂè•ÔºåÂæûÈ†≠ÈñãÂßã
            this.currentIndex = 0;
            this.currentSentence = this.sentences[0];
            this.currentIndex++;
          }
        } else {
          this.currentSentence = this.sentences[this.currentIndex];
          this.currentIndex++;
        }
      }

      // Êõ¥Êñ∞‰ªäÊó•Á∑¥ÁøíÊï∏
      this.todayCount++;
      this.saveProgress();

      return this.currentSentence;
    },

    // Êõ¥Êñ∞ needReviewÔºàÂëºÂè´ APIÔºåÂè™ÊúâÊú¨Ê©üÊúâÊïàÔºâ
    async updateReview(id, needReview) {
      try {
        const response = await fetch('/api/update-review', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            id: id,
            needReview: needReview,
            level: this.currentLevel,
          }),
        });

        const result = await response.json();

        if (result.success) {
          // Êõ¥Êñ∞Êú¨Âú∞Ë≥áÊñô
          const sentence = this.sentences.find(s => s.id === id);
          if (sentence) {
            sentence.needReview = needReview;
          }
          console.log(`‚úÖ ${result.message}`);
          return true;
        } else {
          console.warn(`‚ö†Ô∏è Êõ¥Êñ∞Â§±ÊïóÔºö${result.message}`);
          // GitHub Pages ‰∏äÊúÉÂ§±ÊïóÔºåÈÄôÊòØÊ≠£Â∏∏ÁöÑ
          return false;
        }

      } catch (error) {
        // GitHub Pages ‰∏äÊ≤íÊúâ APIÔºåÊúÉÈÄ≤Âà∞ÈÄôË£°
        console.warn('‚ö†Ô∏è ÁÑ°Ê≥ïÂëºÂè´ APIÔºàÂèØËÉΩÂú® GitHub Pages ‰∏äÔºâ:', error.message);
        
        // Âè™Êõ¥Êñ∞Êú¨Âú∞Ë≥áÊñôÔºà‰∏çÊúÉÂ≠òÂà∞ ExcelÔºâ
        const sentence = this.sentences.find(s => s.id === id);
        if (sentence) {
          sentence.needReview = needReview;
        }
        return false;
      }
    },

    // ÂàáÊèõ needReview
    async toggleReview(id) {
      const sentence = this.sentences.find(s => s.id === id);
      if (sentence) {
        const newValue = sentence.needReview === 'Y' ? 'N' : 'Y';
        await this.updateReview(id, newValue);
      }
    },

    // ÂèñÂæóÂñÆ‰∏ÄÂè•Â≠êÁöÑÂÆåÊï¥Ë≥áË®ä
    getSentenceInfo(id) {
      return this.sentences.find(s => s.id === id) || null;
    },

    // ÈáçÁΩÆÈÄ≤Â∫¶
    resetProgress() {
      this.currentIndex = 0;
      this.newCount = 0;
      this.currentSentence = null;
      this.saveProgress();
    },

    // ÈáçÁΩÆ‰ªäÊó•Ë®àÊï∏
    resetTodayCount() {
      this.todayCount = 0;
      localStorage.setItem('sentence-today-count', '0');
    },
  },
});