  cursor: pointer;
  transition: all 0.2s;
}

.next-btn:hover {
  background: #219a52;
}

/* ÈÄ≤Â∫¶Ë≥áË®ä */
.progress-info {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
