  background: white;
  border-radius: 8px;
  color: #7f8c8d;
  font-size: 0.9rem;
}

/* ÈñãÂßãÊåâÈàï */
.start-btn {
  margin-top: 20px;
  padding: 15px 40px;
  border: none;
  border-radius: 8px;
  background: #3498db;
  color: white;
  font-size: 1.1rem;
  cursor: pointer;
}

.start-btn:hover {
  background: #2980b9;
}
</style>