<template>
  <div class="grammar-info-overlay" @click.self="$emit('close')">
    <div class="grammar-info-panel">
      <!-- Ê®ôÈ°åÂàó -->
      <div class="panel-header">
        <h3>üìù ÊñáÊ≥ïÂâñÊûê</h3>
        <button @click="$emit('close')" class="close-btn">&times;</button>
      </div>

      <!-- ÊñáÊ≥ï‰∏ªÈ°å -->
      <div class="topic-section">
        <span class="topic-tag">{{ passage.grammarTopic }}</span>
        <span v-if="passage.category" class="category-tag">{{ passage.category }}</span>
      </div>

      <!-- ÈÄêÂè•ÂâñÊûê -->
      <div class="sentences-section">
        <div
          v-for="(sentence, index) in passage.sentences"
          :key="index"
          class="sentence-block"
        >
          <div class="sentence-header" @click="toggleSentence(index)">
            <span class="sentence-label">Âè•Â≠ê {{ index + 1 }}</span>
            <span class="toggle-icon">{{ expandedSentences[index] ? '‚ñº' : '‚ñ∂' }}</span>
          </div>

          <!-- Âè•Â≠êÂéüÊñá -->
          <div class="sentence-text">
            {{ sentence.text }}
            <button @click="speakSentence(sentence.text)" class="speak-btn">üîä</button>
          </div>

          <!-- ÂâñÊûêÂÖßÂÆπÔºàÂèØÂ±ïÈñã/Êî∂ÂêàÔºâ -->
          <div v-show="expandedSentences[index]" class="analysis-content">
            <div
              v-for="(item, idx) in sentence.analysis"
              :key="idx"
              class="analysis-item"
            >
              <span class="analysis-label">{{ item.label }}</span>
              <span class="analysis-value">{{ item.content }}</span>
            </div>
          </div>
        </div>
      </div>

      <!-- ÁõÆÊ®ôÂñÆÂ≠ó -->
      <div class="words-section">
        <h4>üìñ ÁõÆÊ®ôÂñÆÂ≠ó</h4>
        <div class="word-list">
          <div
            v-for="word in passage.targetWords"
            :key="word"
            class="word-item"
            @click="showWordDetail(word)"
          >
            <span class="word-text">{{ word }}</span>
            <button @click.stop="speakWord(word)" class="speak-word-btn">üîä</button>
          </div>
        </div>

        <!-- ÂñÆÂ≠óË©≥ÊÉÖ -->
        <div v-if="selectedWord" class="word-detail">
          <div class="word-detail-header">
            <strong>{{ selectedWord }}</strong>
            <span v-if="getWordData(selectedWord, 'wordPartsOfSpeech')" class="word-pos">
              ({{ getWordData(selectedWord, 'wordPartsOfSpeech') }})
            </span>
          </div>
          <p v-if="getWordData(selectedWord, 'wordTranslations')" class="word-translation">
            {{ getWordData(selectedWord, 'wordTranslations') }}
          </p>
          <p v-if="getWordData(selectedWord, 'wordExplanations')" class="word-explanation">
            {{ getWordData(selectedWord, 'wordExplanations') }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useExcelStore } from '@/stores/excelStore';

export default {
  name: 'GrammarInfo',
  props: {
    passage: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      excelStore: useExcelStore(),
      expandedSentences: {},
      selectedWord: null,
    };
  },
  created() {
    // È†êË®≠Â±ïÈñãÊâÄÊúâÂè•Â≠ê
    this.passage.sentences.forEach((_, index) => {
      this.expandedSentences[index] = true;
    });
  },
  methods: {
    toggleSentence(index) {
      this.expandedSentences[index] = !this.expandedSentences[index];
    },

    showWordDetail(word) {
      this.selectedWord = this.selectedWord === word ? null : word;
    },

    getWordData(word, dataType) {
      const lowerWord = word.toLowerCase();
      if (this.excelStore[dataType]) {
        return this.excelStore[dataType][lowerWord] || '';
      }
      return '';
    },

    speakSentence(text) {
      this.speak(text, 0.9);
    },

    speakWord(word) {
      this.speak(word, 0.8);
    },

    speak(text, rate = 0.9) {
      if ('speechSynthesis' in window) {
        const synth = window.speechSynthesis;
        if (synth.speaking) synth.cancel();

        const utterance = new SpeechSynthesisUtterance(text);
        utterance.lang = 'en-US';
        utterance.rate = rate;

        const voices = synth.getVoices();
        const enVoice = voices.find(v => v.lang.startsWith('en-') && v.name.includes('Google')) ||
                        voices.find(v => v.lang.startsWith('en'));
        if (enVoice) {
          utterance.voice = enVoice;
        }

        synth.speak(utterance);
      }
    },
  },
};
</script>

<style scoped>
.grammar-info-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.grammar-info-panel {
  background: white;
  border-radius: 12px;
  max-width: 800px;
  width: 95%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
}

/* Ê®ôÈ°åÂàó */
.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #eee;
  position: sticky;
  top: 0;
  background: white;
  z-index: 1;
}

.panel-header h3 {
  margin: 0;
  color: #2c3e50;
}

.close-btn {
  background: none;
  border: none;
  font-size: 2rem;
  cursor: pointer;
  color: #999;
  line-height: 1;
}

.close-btn:hover {
  color: #333;
}

/* ‰∏ªÈ°åÂçÄ */
.topic-section {
  padding: 15px 20px;
  background: #f8f9fa;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.topic-tag {
  display: inline-block;
  padding: 6px 14px;
  background: #3498db;
  color: white;
  border-radius: 20px;
  font-size: 0.95rem;
  font-weight: 500;
}

.category-tag {
  display: inline-block;
  padding: 6px 14px;
  background: #e8f5e9;
  color: #27ae60;
  border-radius: 20px;
  font-size: 0.9rem;
}

/* Âè•Â≠êÂçÄ */
.sentences-section {
  padding: 20px;
}

.sentence-block {
  margin-bottom: 20px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  overflow: hidden;
}

.sentence-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 15px;
  background: #f5f5f5;
  cursor: pointer;
  user-select: none;
}

.sentence-header:hover {
  background: #eeeeee;
}

.sentence-label {
  font-weight: 600;
  color: #2c3e50;
}

.toggle-icon {
  color: #7f8c8d;
  font-size: 0.8rem;
}

.sentence-text {
  padding: 15px;
  font-size: 1.1rem;
  line-height: 1.6;
  color: #2c3e50;
  background: #fafafa;
  border-bottom: 1px solid #e0e0e0;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 10px;
}

.speak-btn {
  padding: 6px 12px;
  border: none;
  border-radius: 6px;
  background: #3498db;
  color: white;
  cursor: pointer;
  font-size: 0.85rem;
  flex-shrink: 0;
}

.speak-btn:hover {
  background: #2980b9;
}

/* ÂâñÊûêÂÖßÂÆπ */
.analysis-content {
  padding: 15px;
  background: white;
}

.analysis-item {
  padding: 10px 12px;
  margin-bottom: 8px;
  background: #fff9e6;
  border-radius: 6px;
  border-left: 4px solid #f39c12;
}

.analysis-item:last-child {
  margin-bottom: 0;
}

.analysis-label {
  display: inline-block;
  background: #f39c12;
  color: white;
  padding: 3px 10px;
  border-radius: 4px;
  font-size: 0.85rem;
  margin-right: 10px;
  font-weight: 500;
}

.analysis-value {
  color: #2c3e50;
  font-size: 0.95rem;
}

/* ÂñÆÂ≠óÂçÄ */
.words-section {
  padding: 20px;
  border-top: 1px solid #eee;
}

.words-section h4 {
  margin: 0 0 15px 0;
  color: #2c3e50;
}

.word-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 15px;
}

.word-item {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 14px;
  background: #e3f2fd;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.2s;
}

.word-item:hover {
  background: #bbdefb;
}

.word-text {
  color: #1976d2;
  font-weight: 500;
}

.speak-word-btn {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 0.9rem;
  padding: 2px;
}

/* ÂñÆÂ≠óË©≥ÊÉÖ */
.word-detail {
  padding: 15px;
  background: #f8f9fa;
  border-radius: 8px;
  border-left: 4px solid #3498db;
}

.word-detail-header {
  margin-bottom: 8px;
}

.word-detail-header strong {
  font-size: 1.1rem;
  color: #2c3e50;
}

.word-pos {
  color: #7f8c8d;
  font-size: 0.9rem;
  margin-left: 8px;
}

.word-translation {
  color: #27ae60;
  margin: 5px 0;
  font-weight: 500;
}

.word-explanation {
  color: #7f8c8d;
  font-size: 0.95rem;
  margin: 5px 0;
}
</style>