import { createRouter, createWebHistory } from "vue-router";

// Áü≠Âè•Á∑¥ÁøíÈ†ÅÈù¢
import Listsentencelevel from "../views/Listsentencelevel.vue";
import SentencePractice from "../views/SentencePractice.vue";
import GrammarPractice from "../views/GrammarPractice.vue";
import Grammarsubjectverbagreement from "../views/Grammarsubjectverbagreement.vue";

const routes = [
  // Áü≠Âè•Á∑¥ÁøíÈ†ÅÈù¢
  {
    path: "/Listsentencelevel",
    name: "Listsentencelevel",
    component: Listsentencelevel,
  },
  {
    path: "/sentence-level",
    name: "SentenceLevel",
    component: Listsentencelevel,
  },
  {
    path: "/sentence-practice/:level",
    name: "SentencePractice",
    component: SentencePractice,
  },
  {
    path: "/GrammarPractice",
    name: "GrammarPractice",
    component: GrammarPractice,
  },
  {
    path: "/Grammarsubjectverbagreement",
    name: "Grammarsubjectverbagreement",
    component: Grammarsubjectverbagreement,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
