          question: 'A number of issues ___ been raised.',
          options: ['has', 'have'],
          answer: 'have',
          explanation: '"A number of" = manyÔºåË¶ñÁÇ∫Ë§áÊï∏ÔºåÁî® have„ÄÇ'
        },
        {
          question: 'The number of unemployed people ___ rising.',
          options: ['is', 'are'],
          answer: 'is',
          explanation: '"The number of" Êåá„ÄåÊï∏Èáè„ÄçÔºåÊòØÂñÆÊï∏ÔºåÁî® is„ÄÇ'
        },
        {
          question: 'Everyone ___ to pass the exam.',
          options: ['want', 'wants'],
          answer: 'wants',
          explanation: '"Everyone" ÊòØ‰∏çÂÆö‰ª£ÂêçË©ûÔºåÊ∞∏ÈÅ†Áî®ÂñÆÊï∏ÂãïË©û„ÄÇ'
        },
        {
          question: 'The research ___ that the method is effective.',
          options: ['suggest', 'suggests'],
          answer: 'suggests',
          explanation: '"Research" ÊòØ‰∏çÂèØÊï∏ÂêçË©ûÔºåÁî®ÂñÆÊï∏ÂãïË©û„ÄÇ'
        },
      ],
      answers: [],
    };
  },
  computed: {
    allAnswered() {
      return this.answers.filter(a => a !== null).length === this.practiceQuestions.length;
    },
    correctCount() {
      return this.answers.filter((a, i) => a === this.practiceQuestions[i].answer).length;
    },
  },
  created() {
    this.resetPractice();
  },
  methods: {
    selectAnswer(questionIndex, option) {
      this.answers[questionIndex] = option;
    },
    getOptionClass(questionIndex, option) {
      const answer = this.answers[questionIndex];
      if (answer === null) return '';
      
      const correct = this.practiceQuestions[questionIndex].answer;
      if (option === correct) return 'correct';
      if (option === answer && answer !== correct) return 'wrong';
      return '';
    },
    resetPractice() {
      this.answers = new Array(this.practiceQuestions.length).fill(null);
    },
  },
};
</script>

