import { defineStore } from 'pinia';
import * as XLSX from 'xlsx';

export const useGrammarStore = defineStore('grammar', {
  state: () => ({
    passages: [],           // ÊâÄÊúâÊÆµËêΩË≥áÊñô
    currentPassage: null,   // Áï∂ÂâçÊÆµËêΩ
    currentIndex: 0,        // Áï∂ÂâçÁ¥¢Âºï
    isLoading: false,
    error: null,
    filterTopic: '',        // ÊñáÊ≥ï‰∏ªÈ°åÁØ©ÈÅ∏
    filterCategory: '',     // ÂàÜÈ°ûÁØ©ÈÅ∏
    showReviewOnly: false,  // Âè™È°ØÁ§∫ÈúÄË§áÁøí
  }),

  getters: {
    // ÁØ©ÈÅ∏ÂæåÁöÑÊÆµËêΩÂàóË°®
    filteredPassages(state) {
      let result = state.passages;

      if (state.filterTopic) {
        result = result.filter(p => p.grammarTopic === state.filterTopic);
      }

      if (state.filterCategory) {
        result = result.filter(p => p.category === state.filterCategory);
      }

      if (state.showReviewOnly) {
        result = result.filter(p => p.needReview === 'Y');
      }

      return result;
    },

    // ÊâÄÊúâÊñáÊ≥ï‰∏ªÈ°åÔºàÁî®ÊñºÁØ©ÈÅ∏‰∏ãÊãâÔºâ
    allTopics(state) {
      const topics = new Set(state.passages.map(p => p.grammarTopic).filter(Boolean));
      return Array.from(topics).sort();
    },

    // ÊâÄÊúâÂàÜÈ°ûÔºàÁî®ÊñºÁØ©ÈÅ∏‰∏ãÊãâÔºâ
    allCategories(state) {
      const categories = new Set(state.passages.map(p => p.category).filter(Boolean));
      return Array.from(categories).sort();
    },

    // Á∏ΩÊÆµËêΩÊï∏
    totalPassages(state) {
      return state.passages.length;
    },

    // ÁØ©ÈÅ∏ÂæåÁöÑÊÆµËêΩÊï∏
    filteredCount() {
      return this.filteredPassages.length;
    },

    // ÈúÄË§áÁøíÊï∏Èáè
    reviewCount(state) {
      return state.passages.filter(p => p.needReview === 'Y').length;
    },
  },

  actions: {
    // ËºâÂÖ• Excel Ë≥áÊñô
    async loadPassages() {
      this.isLoading = true;
      this.error = null;

      try {
        const url = process.env.BASE_URL + 'excel/grammar-passages.xlsx';
        const response = await fetch(url);
        
        if (!response.ok) {
          throw new Error('ÁÑ°Ê≥ïËºâÂÖ•ÊñáÊ≥ïÊÆµËêΩÊ™îÊ°à');
        }

        const arrayBuffer = await response.arrayBuffer();
        const data = new Uint8Array(arrayBuffer);
        const workbook = XLSX.read(data, { type: 'array' });

        const sheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[sheetName];
        const jsonData = XLSX.utils.sheet_to_json(worksheet);

        // ËΩâÊèõË≥áÊñôÊ†ºÂºè
        this.passages = jsonData.map(row => ({
          passageId: row.passageId || '',
          grammarTopic: row.grammarTopic || '',
          fullPassage: row.fullPassage || '',
          sentences: this.parseSentences(row),
          targetWords: this.parseTargetWords(row.targetWords),
          category: row.category || '',
          needReview: row.needReview || 'N',
        }));

        // Ë®≠ÂÆöÁ¨¨‰∏ÄÁ≠ÜÁÇ∫Áï∂Ââç
        if (this.passages.length > 0) {
          this.currentPassage = this.passages[0];
          this.currentIndex = 0;
        }

      } catch (error) {
        console.error('ËºâÂÖ•ÊñáÊ≥ïÊÆµËêΩÂ§±Êïó:', error);
        this.error = error.message;
      } finally {
        this.isLoading = false;
      }
    },

    // Ëß£ÊûêÂè•Â≠êÂíåÂàÜÊûê
    parseSentences(row) {
      const sentences = [];

      for (let i = 1; i <= 3; i++) {
        const sentence = row[`sentence${i}`];
        const analysis = row[`analysis${i}`];

        if (sentence) {
          sentences.push({
            text: sentence,
            analysis: this.parseAnalysis(analysis),
          });
        }
      }

      return sentences;
    },

    // Ëß£ÊûêÂàÜÊûêÂ≠ó‰∏≤ÁÇ∫ÁµêÊßãÂåñË≥áÊñô
    parseAnalysis(analysisStr) {
      if (!analysisStr) return [];

      // Ê†ºÂºè: ‰∏ªË©û:The archaeologist / ÂãïË©û:had discovered / ...
      const parts = analysisStr.split(' / ');

      return parts.map(part => {
        const colonIndex = part.indexOf(':');
        if (colonIndex > -1) {
          return {
            label: part.substring(0, colonIndex).trim(),
            content: part.substring(colonIndex + 1).trim(),
          };
        }
        return { label: '', content: part.trim() };
      }).filter(item => item.content);
    },

    // Ëß£ÊûêÁõÆÊ®ôÂñÆÂ≠ó
    parseTargetWords(wordsStr) {
      if (!wordsStr) return [];
      return wordsStr.split(',').map(w => w.trim()).filter(Boolean);
    },

    // ‰∏ã‰∏ÄÊÆµ
    nextPassage() {
      const filtered = this.filteredPassages;
      if (filtered.length === 0) return;

      this.currentIndex = (this.currentIndex + 1) % filtered.length;
      this.currentPassage = filtered[this.currentIndex];
    },

    // ‰∏ä‰∏ÄÊÆµ
    prevPassage() {
      const filtered = this.filteredPassages;
      if (filtered.length === 0) return;

      this.currentIndex = (this.currentIndex - 1 + filtered.length) % filtered.length;
      this.currentPassage = filtered[this.currentIndex];
    },

    // Ë∑≥Âà∞ÊåáÂÆöÊÆµËêΩ
    goToPassage(passageId) {
      const index = this.filteredPassages.findIndex(p => p.passageId === passageId);
      if (index > -1) {
        this.currentIndex = index;
        this.currentPassage = this.filteredPassages[index];
      }
    },

    // ÂàáÊèõË§áÁøíÁãÄÊÖã
    async toggleReview(passageId) {
      const passage = this.passages.find(p => p.passageId === passageId);
      if (passage) {
        passage.needReview = passage.needReview === 'Y' ? 'N' : 'Y';
        
        // ÂêåÊ≠•Êõ¥Êñ∞ currentPassage
        if (this.currentPassage?.passageId === passageId) {
          this.currentPassage.needReview = passage.needReview;
        }

        // ÂõûÂØ´ Excel
        await this.saveToExcel();
      }
    },

    // ÂõûÂØ´ ExcelÔºàÈúÄË§áÁøíÁãÄÊÖãÔºâ
    async saveToExcel() {
      try {
        const url = process.env.BASE_URL + 'excel/grammar-passages.xlsx';
        const response = await fetch(url);
        const arrayBuffer = await response.arrayBuffer();
        const data = new Uint8Array(arrayBuffer);
        const workbook = XLSX.read(data, { type: 'array' });

        const sheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[sheetName];

        // Êõ¥Êñ∞ needReview Ê¨Ñ‰Ωç (L Ê¨Ñ)
        this.passages.forEach((passage, index) => {
          const rowNum = index + 2; // Á¨¨‰∏ÄÂàóÊòØÊ®ôÈ°å
          const cellRef = `L${rowNum}`;
          worksheet[cellRef] = { t: 's', v: passage.needReview };
        });

        // Áî¢ÁîüÊõ¥Êñ∞ÂæåÁöÑÊ™îÊ°à
        const wbout = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
        const blob = new Blob([wbout], { type: 'application/octet-stream' });

        // Ëß∏Áôº‰∏ãËºâÔºàËÆì‰ΩøÁî®ËÄÖÊâãÂãïÊõøÊèõÊ™îÊ°àÔºâ
        const downloadUrl = URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = downloadUrl;
        link.download = 'grammar-passages.xlsx';
        link.click();
        URL.revokeObjectURL(downloadUrl);

        console.log('Excel Â∑≤Êõ¥Êñ∞‰∏¶‰∏ãËºâ');
      } catch (error) {
        console.error('ÂÑ≤Â≠ò Excel Â§±Êïó:', error);
      }
    },

    // Ë®≠ÂÆöÁØ©ÈÅ∏Ê¢ù‰ª∂
    setFilter({ topic, category, reviewOnly }) {
      if (topic !== undefined) this.filterTopic = topic;
      if (category !== undefined) this.filterCategory = category;
      if (reviewOnly !== undefined) this.showReviewOnly = reviewOnly;

      // ÈáçÁΩÆÂà∞Á¨¨‰∏ÄÁ≠Ü
      if (this.filteredPassages.length > 0) {
        this.currentIndex = 0;
        this.currentPassage = this.filteredPassages[0];
      } else {
        this.currentPassage = null;
      }
    },

    // Ê∏ÖÈô§ÁØ©ÈÅ∏
    clearFilter() {
      this.filterTopic = '';
      this.filterCategory = '';
      this.showReviewOnly = false;

      if (this.passages.length > 0) {
        this.currentIndex = 0;
        this.currentPassage = this.passages[0];
      }
    },
  },
});